<template>
    <yd-layout class="schBg">
        <div class="handleTabBox">
            <yd-flexbox>
                <yd-flexbox-item>
                    <div class="tabTopL">待办</div>
                </yd-flexbox-item>
                <yd-flexbox-item>
                    <div @click="closeWebView" class="tabTopR"><img class="imgWork" src="../../../static/images/home@2x.png">工作中心</div>
                </yd-flexbox-item>
            </yd-flexbox>
            <div class="tabBottom">
                <router-link to=""><span class="tabIcon active">待办</span></router-link>
                <router-link to="/performanceList"><span class="tabIcon">绩效</span></router-link>
                <router-link to="/meetingManage"><span class="tabIcon">会议</span></router-link>
                <span class="tabIcon" @click="jumpSchedule()">日程</span>
            </div>
        </div>
        <div class="mainContentFluid">
            <div class="mainContentFluid" v-show="status ==0" :class="{autoBox: waitList.length <= 0 }">
                <div class="newSchdlue" @click="jijianFun" v-show="jijianArr.length!=0">
                    <div class="news">
                        <span class="redCircle"></span>
                        <span class="text">{{`${jijianArr.length}个急件`}}</span>
                    </div>
                </div>
                <div class=" handleList" v-if="waitList.length<1">
                    <div class="noBox">
                        <img src="../../../static/images/label/noHandle.png">
                    </div>
                        <p class="noWord">您暂时没有流程待处理</p>
                    </div>
                    <div class="listWidth handleList" v-else>
                        <yd-flexbox class="listHandle" direction="vertical" v-for="(item, index) in waitFilter" :key="index" @click.native="page_jump(item.parentProDefName,item.workItemId,item.activityDefId,item.procBossState,0)">
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="handleTitle">
                                        <div class="tit">{{`${item.title}`}}</div>
                                        <div v-if="item.parentProDefName.indexOf('qianbao') != -1" class="titleBtn">签报</div>
                                        <div v-else-if="item.parentProDefName.indexOf('fawen') != -1" class="titleBtn titleBtn2">发文</div>
                                        <div v-else-if="item.parentProDefName == 'shouwen_main'" class="titleBtn titleBtn3">收文</div>
                                        <div v-else-if="item.parentProDefName.indexOf('shouwen_zgsbm') != -1" class="titleBtn titleBtn3">部门收文</div>
                                        <div v-else-if="item.parentProDefName.indexOf('ssist') != -1" class="titleBtn titleBtn4">内部协办</div>
                                        <div v-else-if="item.parentProDefName.indexOf('askforleave') != -1" class="titleBtn titleBtn5">请休假</div>
                                        <div v-else-if="item.parentProDefName.indexOf('TravelRemarkMain') != -1" class="titleBtn titleBtn6">出差审批</div>
                                        <div v-else-if="item.parentProDefName.indexOf('officialSealMain') != -1" class="titleBtn titleBtn6">公章审批</div>
                                        <div v-else-if="item.parentProDefName.indexOf('cross_system_approval_main') != -1" class="titleBtn titleBtn3">业务审批</div>
                                        <div v-else-if="item.parentProDefName.indexOf('cross_sys_sim_appr_main') != -1" class="titleBtn titleBtn3">收入证明</div>
                                        <div v-else-if="item.parentProDefName.indexOf('troubleshooting_main') != -1" class="titleBtn titleBtn2">营销受理</div>
                                        <div v-else class="titleBtn3"></div>
                                    </div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="start">发起时间：</div>
                                    <div class="startDate" v-text="item.startDate"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="start">发起部门：</div>
                                    <div class="startDate" v-text="item.firstDeptName"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </div>
                </div>
                <div class="mainContentFluid" v-if="status == 1">
                    <div class="listWidth handleList">
                        <yd-flexbox class="listHandle" direction="vertical" v-for="(item, index) in hasFilter" :key="index" @click.native="page_jump(item.parentProDefName,item.workItemId,item.activityDefId,item.procBossState,1)">
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="handleTitle">
                                        <div class="tit">{{`${item.title}`}}</div>
                                        <div v-if="item.parentProDefName.indexOf('qianbao') != -1" class="titleBtn">签报</div>
                                        <div v-else-if="item.parentProDefName.indexOf('fawen') != -1" class="titleBtn titleBtn2">发文</div>
                                        <div v-else-if="item.parentProDefName == 'shouwen_main'" class="titleBtn titleBtn3">收文</div>
                                        <div v-else-if="item.parentProDefName.indexOf('shouwen_zgsbm') != -1" class="titleBtn titleBtn3">部门收文</div>
                                        <div v-else-if="item.parentProDefName.indexOf('ssist') != -1" class="titleBtn titleBtn4">内部协办</div>
                                        <div v-else-if="item.parentProDefName.indexOf('askforleave') != -1" class="titleBtn titleBtn5">请休假</div>
                                        <div v-else-if="item.parentProDefName.indexOf('TravelRemarkMain') != -1" class="titleBtn titleBtn6">出差审批</div>
                                        <div v-else-if="item.parentProDefName.indexOf('officialSealMain') != -1" class="titleBtn titleBtn6">公章审批</div>
                                        <div v-else-if="item.parentProDefName.indexOf('cross_system_approval_main') != -1" class="titleBtn titleBtn3">业务审批</div>
                                        <div v-else-if="item.parentProDefName.indexOf('cross_sys_sim_appr_main') != -1" class="titleBtn titleBtn3">收入证明</div>
                                        <div v-else-if="item.parentProDefName.indexOf('troubleshooting_main') != -1" class="titleBtn titleBtn2">营销受理</div>
                                        <div v-else class="titleBtn3"></div>
                                    </div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="start">发起时间：</div>
                                    <div class="startDate" v-text="item.startDate"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="start">发起部门：</div>
                                    <div class="startDate" v-text="item.firstDeptName"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </div>
                </div>
                <div class="filterBox" v-show="filterFlag">
                    <ul>
                        <li @click="filterIndex = index;filterFun(filterArr1[index])" :class="{active: index == filterIndex}" v-for="(item, index) in filterArr" :key="index">{{item}}</li>
                    </ul>
                </div>
            </div>
            <yd-tabbar class="schedules" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
                <yd-tabbar-item title="待办" link="#" v-bind:class="{btnActive:status==0,'topBor' : true}" v-on:click.native="status=0;">
                    <yd-icon name="waitHandle1" slot="icon" size="0.54rem"></yd-icon>
                </yd-tabbar-item>
                <yd-tabbar-item title="已办" link="#" v-bind:class="{btnActive:status==1}" v-on:click.native="status=1;hasHandle()">
                    <yd-icon name="hasHandle1" slot="icon" size="0.54rem"></yd-icon>
                </yd-tabbar-item>
                <div class="schSai">
                    <img v-show="!filterFlag" @click="filterFlag = true" src="../../../static/images/handle_sai.png">
                    <img v-show="filterFlag" @click="filterFlag = false" src="../../../static/images/handle_sai1.png">
            </div>
            </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapState, mapMutations } from 'vuex';
export default {
    name: 'handle',
    data() {
        return {
            status: 0,
            firstIn: true,
            toggleTxt: 1,
            detailArr: [],
            waitList: [],
            waitFilter: [],
            jijianArr: [],
            activeFlag: false,
            filterFlag: false,
            filterIndex: 0,
            filterArr: ['全部', '签报', '发文', '收文', '请休假', '内部协办', '出差审批', '部门收文', '公章审批', '业务审批', '营销受理'],
            filterArr1: ['all', 'qianbao', 'fawen', 'shouwen_main', 'askforleave', 'ssist', 'TravelRemarkMain', 'shouwen_zgsbm', 'officialSealMain', 'cross_system_approval_main', 'troubleshooting_main'],
            hasList: [],
            hasFilter: []
        }
    },
    computed: {
        ...mapState([
            'userInfo'
        ])
    },
    mounted() {
        const vm = this;
        vm.waitHandle();
        vm.setTitleName('待办已办');
        document.setTitle('待办已办');
        var setNavigationBarTitle = function(title) {
            UPCHAT.M.NAPI.setNavigationBarTitle(title);
        };
        setTimeout(function() {
            var title = '待办已办';
            if (UPCHAT.M.isNative()) {
                UPCHAT.M.init(setNavigationBarTitle(title));
            }
        }, 200);
        vm.setHandleInfo({ keepAlive: [] });
        vm.setHandleInfo({ file_url: [] });
        vm.setSealInfo({
            applyoffsname: '',
            approver: [],
            approveType: '',
            applyoffsid: '',
            jsonData: '',
            fuyan: '',
            sealArray: [],
            fileNum: 0,
            lineNum: -1
        })
        vm.setIncomeInfo({
            jsonData: {},
            aproveList: [],
            approver: [],
            secretariesList: [],
            fuyan: []
        })
        vm.setSystemInfo({
            jsonData: '',
            fuyan: ''
        })
        vm.setTravelInfo({
            formJson: {},
            approver: [],
            secretariesList: [],
            applyList: [],
            reason: '',
            confirmItem: '',
            startdate: '',
            enddate: '',
            applicantid: '',
            traveldays: '',
            travelplace: '',
            othertravelnames: '',
            selectothertravelnames: {},
            reason2: '',
            firstorgname: '',
            applicantUserId: '',
            statusRadio: 0,
            jsonData: {},
            othertravelids: ''
        })
        var info1 = {};
        info1.keepAlive = [];
        info1.keepData = '';
        info1.mainText = '';
        vm.setHandleInfo(info1);
    },
    beforeRouteLeave(to, from, next) {
        // 设置下一个路由的 meta
        to.meta.keepAlive = false; // B 跳转到 A 时，让 A 缓存，即不刷新
        next();
    },
    methods: {
        ...mapMutations([
            'setUserInfo', 'setHandleInfo', 'setTitleName', 'setSealInfo', 'setSystemInfo', 'setIncomeInfo', 'setTravelInfo'
        ]),
        waitHandle() {
            var vm = this;
            vm.toggleTxt = 1;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/taskList', {
                'type': 'oa4Qianbao',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                var data = res.jsonData;
                if (data != '' && data != null && data != undefined) {
                    vm.waitList = data;
                    vm.waitFilter = vm.waitList;
                    vm.jijianArr = vm.waitList.filter((item, index) => {
                        return item.title.indexOf('【急件】') == 0 || item.title.indexOf('【特急】') == 0;
                    });
                }
            });
        },
        hasHandle() {
            var vm = this;
            vm.toggleTxt = 2;
            if (vm.firstIn) {
                vm.firstIn = false;
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/fintaskList', {
                    'type': 'oa4Qianbao',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                }, function(res) {
                    var data = res.jsonData;
                    if (data != '' || data != null || data != undefined) {
                        vm.hasList = data;
                    }
                    vm.hasFilter = vm.hasList;
                });
            }
        },
        jijianFun() {
            const vm = this;
            vm.waitFilter = vm.jijianArr;
        },
        filterFun(name) {
            if (name == 'all') {
                this.waitFilter = this.waitList;
                this.hasFilter = this.hasList;
                return;
            }
            this.waitFilter = this.waitList.filter((item, index) => {
                return item.parentProDefName.indexOf(name) != -1;
            })
            this.hasFilter = this.hasList.filter((item, index) => {
                return item.parentProDefName.indexOf(name) != -1;
            })
        },
        page_jump(parentProDefName, workItemId, activityDefId, procBossState, type) {
            var vm = this;
            var Info = {};
            Info.userid = vm.userInfo.userid;
            Info.workItemID = workItemId;
            var info1 = {};
            info1.activityDefId = activityDefId;
            info1.keepAlive = [];
            info1.keepData = '';
            info1.mainText = '';
            vm.setSealInfo({
                jsonData: '',
                fuyan: '',
                approver: []
            })
            vm.setHandleInfo(info1);
            if (parentProDefName.indexOf('qianbao') != -1) {
                Info.type = 'oa4Qianbao';
                vm.setUserInfo(Info);
                // type 0是待办 1是已办
                vm.$router.push('/unionPayLabel/' + Info.type + '/' + Info.workItemID + '/' + info1.activityDefId + '/' + type);
            } else if (parentProDefName.indexOf('fawen') != -1) {
                Info.type = 'oa4Fawen';
                vm.setUserInfo(Info);
                vm.$router.push('/unionPaySend/' + Info.type + '/' + Info.workItemID + '/' + activityDefId + '/' + type);
            } else if (parentProDefName == 'shouwen_main') {
                Info.type = 'oa4Shouwen';
                vm.setUserInfo(Info);
                vm.$router.push('/unionPayReceived/' + Info.type + '/' + Info.workItemID + '/' + activityDefId + '/' + type);
            } else if (parentProDefName == 'shouwen_zgsbm') {
                Info.type = 'oa4Shouwen';
                vm.setUserInfo(Info);
                vm.$router.push('/unionPayReceivedHeadquarters/' + Info.type + '/' + Info.workItemID + '/' + activityDefId + '/' + type);
            } else if (parentProDefName.indexOf('ssist') != -1) {
                Info.type = 'internalAssist';
                vm.setUserInfo(Info);
                vm.$router.push('/innerAssist/' + Info.type + '/' + Info.workItemID + '/' + activityDefId + '/' + type);
            } else if (parentProDefName.indexOf('askforleave') != -1) {
                Info.type = 'askforleave';
                vm.setUserInfo(Info);
                if (procBossState == '1') {
                    vm.$router.push('/approveHoliday/' + workItemId + '/' + type);
                } else {
                    if (activityDefId.indexOf('secretaryActivity') != -1) {
                        vm.$router.push('/approveHoliday/true/' + workItemId + '/' + type);
                    } else if (activityDefId.indexOf('bossActivity') != -1) {
                        vm.$router.push('/approveHoliday/' + workItemId + '/' + type);
                    } else if (activityDefId.indexOf('acceptActivity') != -1) {
                        vm.$router.push('/approveHoliday/false/' + workItemId + '/' + type);
                    } else if (activityDefId.indexOf('logoutActivity') != -1) {
                        vm.$router.push('/lastConfirm/' + workItemId + '/' + type);
                    } else {
                        vm.$router.push('/selected/' + workItemId + '/' + type);
                    }
                }
            } else if (parentProDefName.indexOf('TravelRemarkMain') != -1) {
                Info.type = 'travelremark';
                vm.setUserInfo(Info);
                if (procBossState == '1') {
                    vm.$router.push('/tripApprove/' + type + '/' + workItemId);
                } else {
                    if (activityDefId.indexOf('travelLeaderActivity') != -1 || activityDefId.indexOf('travelKnownActivity') != -1) {
                        vm.$router.push('/tripApprove/' + type + '/' + workItemId);
                    } else if (activityDefId.indexOf('travelConfirmActivity') != -1) {
                        vm.$router.push('/confirmTrip/' + type + '/' + workItemId);
                    } else if (activityDefId.indexOf('travelSecrataryActivity') != -1) {
                        vm.$router.push('/tripApprove/true' + '/' + activityDefId + '/' + type + '/' + workItemId);
                    } else {
                        vm.$router.push('/tripForm/true/' + type + '/' + workItemId);
                    }
                }
            } else if (parentProDefName.indexOf('officialSealMain') != -1) {
                if (procBossState == '1') {
                    vm.$router.push('/applicationForm/' + type + '/' + 1 + '/' + workItemId);
                } else {
                    if (activityDefId.indexOf('officialSeal_submitApplication') != -1) {
                        vm.$router.push('/applicationForm/' + type + '/' + 2 + '/' + workItemId);
                    } else if (activityDefId.indexOf('officialSeal_secretaryApproval') != -1) {
                        vm.$router.push('/applicationForm/' + type + '/' + 3 + '/' + workItemId);
                    } else if (activityDefId.indexOf('officialSeal_leaderApproval') != -1) {
                        vm.$router.push('/applicationForm/' + type + '/' + 1 + '/' + workItemId);
                    } else if (activityDefId.indexOf('officialSeal_acceptApplication') != -1) {
                        vm.$router.push('/applicationForm/' + type + '/' + 4 + '/' + workItemId);
                    }
                }
            } else if (parentProDefName.indexOf('cross_system_approval_main') != -1) {
                if (procBossState == '1') {
                    vm.$router.push('/acrossSystem/' + type + '/' + 1 + '/' + workItemId);
                } else {
                    if (activityDefId.indexOf('secrataryAppActivity') != -1) {
                        vm.$router.push('/acrossSystem/' + type + '/' + 2 + '/' + workItemId);
                    } else if (activityDefId.indexOf('leaderAppActivity') != -1) {
                        vm.$router.push('/acrossSystem/' + type + '/' + 1 + '/' + workItemId);
                    }
                }
            } else if (parentProDefName.indexOf('cross_sys_sim_appr_main') != -1) {
                if (procBossState == '1') {
                    vm.$router.push('/approveForm/2/' + workItemId + '/' + type);
                } else {
                    if (activityDefId.indexOf('simSendReqActivity') != -1) {
                        vm.$router.push('/approveForm/1/' + workItemId + '/' + type);
                    } else if (activityDefId.indexOf('simAppActivity') != -1) {
                        vm.$router.push('/approveForm/3/' + workItemId + '/' + type);
                    } else if (activityDefId.indexOf('simResultActivity') != -1) {
                        vm.$router.push('/approveForm/4/' + workItemId + '/' + type);
                    }
                }
            } else if (parentProDefName.indexOf('troubleshooting_main') != -1) {
                if (procBossState == '0') {
                    if (activityDefId.indexOf('handleResultActivity') != -1) {
                        vm.$router.push('/fdApproForm/1/' + workItemId + '/' + type);
                    } else if (activityDefId.indexOf('resultConfirmActivity') != -1) {
                        vm.$router.push('/fdApproForm/2/' + workItemId + '/' + type);
                    } else if (activityDefId.indexOf('submitResultActivity') != -1) {
                        vm.$router.push('/feedbackForm/' + workItemId + '/' + type);
                    }
                }
            } else {
                Info.type = 'oa4Qianbao';
                vm.setUserInfo(Info);
                vm.$router.push('/unionPayLabel/' + Info.type + '/' + Info.workItemID + '/' + info1.activityDefId + '/' + type);
            }
        },
        // 跳转到日程
        jumpSchedule() {
            let vm = this;
            vm.$router.push({
                name: 'mySchedule',
                params: {
                    schId: vm.userInfo.empcode,
                    schFlag: 0,
                    schName: 'my'
                }
            });
        }
    }
}
</script>
<style lang="less" scoped>
.listWidth {
    padding: 0.12rem;
    background: #f8f8f8;
}

.newSchdlue {
    width: 2.1rem;
    height: 0.52rem;
    line-height: 0.52rem;
    font-size: 0.26rem;
    background: #a5a5a5;
    color: #fff;
    border-radius: 0.05rem;
    margin: 0.24rem auto 0.16rem;

    .news {
        background: url(../../../static/images/arrow@2x.png) no-repeat;
        width: 2.1rem;
        height: 0.52rem;
        background-position: 1.5rem 0.13rem;
        background-size: 0.15rem;
    }

    span {
        float: left;
    }

    .redCircle {
        width: 0.12rem;
        height: 0.12rem;
        float: left;
        border-radius: 50%;
        background: #ff2a12;
        margin-top: 0.2rem;
        margin-left: 0.18rem;
        margin-right: 0.14rem;
    }

    // .jijianImg {
    //     width: .1rem;
    // }
    .text {}
}

.mainContentFluid {
    position: relative;
    background: #f8f8f8;

    .filterBox {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        background: #f8f8f8;
        height: 100%;
        padding-left: 0.32rem;
        padding-top: 0.2rem;

        li {
            float: left;
            width: 2.03rem;
            height: 0.67rem;
            border-radius: 0.12rem;
            background: #e4e4e4;
            margin-right: 0.36rem;
            margin-bottom: 0.32rem;
            text-align: center;
            line-height: 0.67rem;
            font-size: 0.3rem;
            color: #9b9b9b;

            &.active {
                background: #ffb221;
                color: #fff;
            }
        }
    }
}

.schedules {
    position: relative;

    .schSai {
        position: absolute;
        top: -1.9rem;
        right: 0.2rem;

        img {
            width: 1.4rem;
            height: 1.4rem;
        }
    }
}

.fluidWidth {
    padding-left: 0;
    padding-right: 0;
}

.refresh {
    position: fixed;
    bottom: 0.36rem;
    right: 0.23rem;
    width: 0.72rem;
    height: 0.72rem;
    -webkit-box-shadow: 0px 4px 5px 2px #ebebeb;
    box-shadow: 0px 4px 7px 2px #ebebeb;
    display: block;
    cursor: pointer;
}

.handleMenu {
    width: 100%;
    height: 1rem;
    background-color: #fff;
    padding-top: 0.18rem;
    padding-bottom: 0.18rem;
    overflow: hidden;
}

.handleMenu .yd-tabbar {
    height: 0.7rem;
    padding: 0;
    overflow: hidden;

    a {
        height: 0.7rem;
    }
}

.autoBox {
    height: calc(100%-2.11rem);
}

.handleTitle {
    width: 100%;
    font-size: 0.3rem;
    font-weight: bold;
    line-height: 0.48rem;
    position: relative;

    .tit {
        width: 5rem;
        word-break: break-all;
    }

    .titleBtn {
        position: absolute;
        right: 0;
        top: 0;
    }
}

.titleBtn {
    min-width: 1.2rem;
    padding-left: .1rem;
    padding-right: .1rem;
    margin-top: 0.04rem;
    height: 0.44rem;
    text-align: center;
    line-height: 0.44rem;
    background-color: #ff5b2f;
    border-radius: 0.06rem;
    color: #fff;
    font-size: 0.26rem;
    font-weight: bold;
    display: inline-block;
    /* -webkit-box-shadow: 0px 4px 7px 2px #ffc6c2;
    box-shadow: 0px 4px 7px 2px #ffc6c2;*/
    /*margin-right: 1.8rem;*/
}

.titleBtn2 {
    background-color: #ffac38;
    /*-webkit-box-shadow: 0px 4px 7px 2px #ffe6bb;
    box-shadow: 0px 4px 7px 2px #ffe6bb;*/
}

.titleBtn3 {
    background-color: #72c325;
    /*-webkit-box-shadow: 0px 4px 7px 2px #d0efbd;
    box-shadow: 0px 4px 7px 2px #d0efbd;*/
}

.titleBtn4 {
    background-color: #1fc1de;
    /*-webkit-box-shadow: 0px 4px 7px 2px #d0efbd;
    box-shadow: 0px 4px 7px 2px #d0efbd;*/
}

